.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.products-container{
  width: 100%;
  height: auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background-color: #61dafb;
  padding: 100px 0;
}

.products-cards-container{
  display: flex;
  height: auto;
  width: 90%;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}


.product-card{
  width: 350px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 30px;
  margin: 20px;
  border-radius: 20px;
  background-color: white;
}

.product-card img{
  width: 250px;
  height: 250px;
  object-fit: contain;
}

.product-card h6{
  margin-top: 10px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.product-card p{
  margin-bottom: 10px;
}

button{
  padding: 15px 30px;
  border: none;
  background-color: black;
  color: white;
  cursor: pointer;  
  position: relative;
  margin: 5px;
}

.added::before{
  content: 'Added';
  height: 100%;
  width: 100%;
  position: absolute;
  top:0;
  left: 0;
  background-color: greenyellow;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor:auto;
}


nav{
  height: 60px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0px 30px;
  overflow: hidden;
  background-color: deepskyblue;
  z-index: 99;
}

nav a{
  padding: 20px 35px;
  text-decoration: none;
  color: black;
}

nav a:hover{
  background-color: black;
  color: white;
}

.active{
  background-color: black;
  color: white;
}

.login-container{
  height: auto;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #61dafb;
}

input{
  margin: 20px;
  outline: none;
  border: none;
  background-color: transparent;
  padding: 10px;
  border-bottom: 2px solid black;
}

input::placeholder{
  color: black;
}

.product-container{
  height: auto;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 100px 0px;
}

.product-top{
  display: flex;
  height: auto;
  width: 90%;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.product-image{
  width: 500px;
}

.product-image img{
  object-fit: contain;
}

.product-text{
  width: 500px;
  margin-left: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: left;
}

.data{
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.product-text p{
  margin: 10px 0;
}

.data h3, .data h4{
  margin-bottom: 10px;
}

.product-bottom{
  width: 90%;
  height: auto;
  overflow-x: hidden;
  margin-top: 50px;
}

.product-bottom img{
  height: 200px;
  width: 200px;
  object-fit: cover;
  margin: 5px;
}

